export default "import React, { FC } from \"react\";\nimport { StyledProductButton } from \"@components/Product/components/Button/Button.style\";\nimport { ButtonProps } from \"@material-ui/core\";\n\ntype ProductButtonProps = ButtonProps;\n\nexport const ProductButton: FC<ProductButtonProps> = ({\n    variant = \"contained\",\n    color = \"primary\",\n    size = \"large\",\n    children,\n    ...buttonProps\n}) => (\n    <StyledProductButton\n        variant={variant}\n        color={color}\n        size={size}\n        {...buttonProps}\n    >\n        {children}\n    </StyledProductButton>\n);\n";